.nav {
    padding-top: 2%;
}

.nav-item {
    color: black;
}

.nav-pills .nav-link.active,
.nav-pills .show>.nav-link {
    color: white;
    background-color: rgb(44, 44, 44);
    border: 1px solid #ccc;
   
}

.nav-link:hover {
    color: black;
}

.nav-link {
    transition: inherit;
}

